import React, { Component } from 'react'
import { Flex, WingBlank, WhiteSpace, NavBar, Toast } from 'antd-mobile'

import { Link } from 'react-router-dom'

import styles from './index.module.css'
import { login } from '../../utils/api/user'
import { setLocal, WNZF_TOKEN } from '../../utils/quanju'

import { withFormik } from 'formik';

import * as yup from 'yup'
// 
const REG_UNAME = /^[a-zA-Z_\d]{5,8}$/
const REG_PWD = /^[a-zA-Z_\d]{5,12}$/

class Login extends Component {
  render() {
    const {
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit,
    } = this.props;
    console.log(this.props);
    
    return (
      <div className={styles.root}>
        {/*  */}
        <NavBar mode="light">
          
        </NavBar>
        <WhiteSpace size="xl" />

        {/*  */}
        <WingBlank>
          <form onSubmit={handleSubmit}>
            <div className={styles.formItem}>
              <input
                className={styles.input}
                name="username"
                value={values.username}
                onChange={handleChange}
                placeholder=""
              />
            </div>
            {/* 58 */}
            {errors.username && <div className={styles.error}>{errors.username}</div>}
            <div className={styles.formItem}>
              <input
                className={styles.input}
                name="password"
                type="password"
                value={values.password}
                onChange={handleChange}
                placeholder=""
              />
            </div>
            {/* 512 */}
            {errors.password && <div className={styles.error}>{errors.password}</div>}
            <div className={styles.formSubmit}>
              <button className={styles.submit} type="submit">
                 
              </button>
            </div>
          </form>
          <Flex className={styles.backHome}>
            <Flex.Item>
              <Link to="/registe">~</Link>
            </Flex.Item>
          </Flex>
        </WingBlank>
      </div>
    )
  }
}
//  => 
const NewLogin = withFormik({
  //   => inputname
  mapPropsToValues: () => ({ username: '', password: '' }),
  //  ? => 
  validationSchema: yup.object().shape({
    username: yup.string().required('').matches(REG_UNAME, '58'),
    password: yup.string().required('').matches(REG_PWD, '512'),
  }),
  handleSubmit: async (values, { props: { history, location } }) => {
    // 
    const { username, password } = values;
    const { status, data, description } = await login({ username, password });
    if (status === 200) {
      Toast.success(description, 2);
      // token
      setLocal(WNZF_TOKEN, data.token);
      // 
      // 
      // if(location.data && location.data.backUrl) {
        // ?. 
      if(location.data?.backUrl) {
        history.push(location.data.backUrl)
      } else {
        history.push('/home/profile')
      }
      
    } else {
      Toast.fail(description, 2)
    }
  }
})(Login);

export default NewLogin
