import React, { Component } from 'react'

import { Link } from 'react-router-dom'
import { Grid, Button, Modal, Toast } from 'antd-mobile'

import { BASE_URL } from '../../utils/axios'

import styles from './index.module.css'
import { isAuth, delToken } from '../../utils/quanju'
import { getUserData, logout } from '../../utils/api/user'

// 
const menus = [
  { id: 1, name: '', iconfont: 'icon-coll', to: '/favorate' },
  { id: 2, name: '', iconfont: 'icon-ind', to: '/rent' },
  { id: 3, name: '', iconfont: 'icon-record' },
  {
    id: 4,
    name: '',
    iconfont: 'icon-identity'
  },
  { id: 5, name: '', iconfont: 'icon-myinfo' },
  { id: 6, name: '', iconfont: 'icon-cust' }
]

// 
const DEFAULT_AVATAR = BASE_URL + '/img/profile/avatar.png'

export default class Profile extends Component {

  // 
  state = {
    // 
    isLogin: isAuth(),
    userInfo: {}
  }

  componentDidMount() {
    this.getUserInfo()
  }

  //  =>  => 
  getUserInfo = async () => {
    const { isLogin, userInfo } = this.state;
    //  => 
    if (isLogin) {
      let { status, data } = await getUserData();
      if (status === 200) {
        // 
        if (data.avatar) {
          data.avatar = `${BASE_URL}$(data.avatar)`
        }
        this.setState({
          userInfo: data
        })
      }
    } else {
      // token
    }
  }

  // 
  logout = () => {
    //  = 
    Modal.alert('', '???', [
      { text: '' },
      {
        text: '', onPress: async () => {
          //  => 
          let { status, description } = await logout();
          if (status === 200) {
            // 
            Toast.info(description, 2);
            // token => 
            delToken()
            // 
            // this.props.history.push('/login')
            // 
            this.setState({
              isLogin: false,
              userInfo: {}
            })
          }
        }
      },
    ])
  }
  render() {
    const { history } = this.props
    const { userInfo, isLogin } = this.state;

    return (
      <div className={styles.root}>
        {/*  */}
        <div className={styles.title}>
          <img
            className={styles.bg}
            src={BASE_URL + '/img/profile/bg.png'}
            alt=""
          />
          {/*  */}
          <div className={styles.info}>
            <div className={styles.myIcon}>
              <img className={styles.avatar} src={userInfo.avatar || DEFAULT_AVATAR} alt="icon" />
            </div>
            <div className={styles.user}>
              <div className={styles.name}>{userInfo.nickname ?? ''}</div>
              {/*  */}
              {
                isLogin && <>
                <div className={styles.auth}>
                  <span onClick={this.logout}></span>
                </div>
                  <div className={styles.edit} onClick={
                    () => history.push('/rent/add')
                  }>
                    
                  <span className={styles.arrow}>
                    <i className="iconfont icon-arrow" />
                  </span>
                </div>
              </>}

              {/*  */}
              {
                !isLogin ? <div className={styles.edit}>
                <Button
                  type="primary"
                  size="small"
                  inline
                  onClick={() => history.push('/login')}
                >
                  
                </Button>
              </div> : null}
            </div>
          </div>
        </div>

        {/*  */}
        <Grid
          data={menus}
          columnNum={3}
          hasLine={false}
          renderItem={item =>
            item.to ? (
              <Link to={item.to}>
                <div className={styles.menuItem}>
                  <i className={`iconfont ${item.iconfont}`} />
                  <span>{item.name}</span>
                </div>
              </Link>
            ) : (
                <div className={styles.menuItem}>
                  <i className={`iconfont ${item.iconfont}`} />
                  <span>{item.name}</span>
                </div>
              )
          }
        />

        {/*  */}
        <div className={styles.ad}>
          <img src={BASE_URL + '/img/profile/join.png'} alt="" />
        </div>
      </div>
    )
  }
}
